import React, { Component } from 'react'

export class TodoItem extends Component {

    getStyle = () => {

        if(this.props.nameItem.completed){

            return {
 
                textDecoration: 'line-through',
                color:'green',
                background:'#E5DEDD'
                
 
             }
         } else{
 
             return {
                 textDecoration: 'none',
                 background: '#E5DEDD'

             }
 
         }

       
      


    }

    

    render() {

        const { id, title  } = this.props.nameItem
        return (
            <div style={{margin:'5px', padding:'5px'}}>
                
                <h1 style={ this.getStyle() } >

                   <label><input type="checkbox" id="x" onChange= {this.props.markComplete.bind(this,id)} style={{margin:'10px', padding:'10px',}} /></label>
                    
                    
                       { title } 

                      <button  style={crossBtnStyle} onClick={this.props.deleteTask.bind(this,id)}> X </button>

                      
                 
                </h1>
            </div>
        )
    }

    



}

const crossBtnStyle= {

    background: 'red',
    color: 'white',
    padding: '4px' ,
    margin:'8px',
    float: 'right',
    borderRadius: '50%',
    border: 'none',
    cursor: 'pointer',
    width:'40px',
    height:'40px',
    fontSize: "20px",
    
    
    
    
    



}



export default TodoItem
