import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import Card from "react-bootstrap/Card";
import Container from 'react-bootstrap/Container';
export default function About() {
    return (
        <div>

            
           
            

            <Container style={{display: 'flex'}}> 
            <Card style={{ width: '100%',backgroundColor:'grey',borderRadius:'50px',textAlign: 'center', }}>
            <Card.Body style={{margin:'10px'}}>
                <Card.Title><h2 style={{color:'white'}}><u>About this app:</u></h2></Card.Title>
                <Card.Subtitle className="mb-2 text-muted" ><p style={{color:'black'}}>(Version 0.0.0.2)</p></Card.Subtitle>
                <Card.Text>
                <p style={{color:'black'}}><b>This is a Todo list app developed by Galib Ahmed Turjo.  </b></p>
                </Card.Text>
                <p><h4 style={{color:'white'}}><u>About me:</u></h4></p>
                <p style={{color:'black'}}><b>Currently I am learning React JS and this is my first project.</b></p>
                <p><h4 style={{color:'white'}}><u>Find me on:</u></h4></p>

                <button type="button" class="btn btn-light" style={{margin:'5px',padding:'5px'}}><Card.Link href="https://github.com/turj00" style={{color:'black'}}><b>Github</b></Card.Link></button>

                <button type="button" class="btn btn-primary" style={{margin:'5px',padding:'5px'}} ><Card.Link href="https://www.facebook.com/turj00.13/" style={{color:'white'}}><b>Facebook</b></Card.Link></button>

                <button type="button" class="btn btn-danger" style={{margin:'5px',padding:'5px'}}><Card.Link href="https://www.instagram.com/turj0.0/" style={{color:'white'}}><b>Instagram</b></Card.Link></button>
                
            </Card.Body>
            </Card>
            </Container>
            
            
        </div>
    )
}
