import React from 'react'
import {Link} from 'react-router-dom' 
//for about and home page trasition 

function Header() {
    return (
        
        <div>
            
            <header style={headerStyle}>
                
                <h1> ToDo List</h1>
                <button type="button" class="btn btn-secondary" ><Link to="/" style={linkStyle}>Home </Link></button> || <button type="button" class="btn btn-secondary"><Link to="/about" style= {linkStyle}>About</Link></button> 


            </header>
            
            
            
        </div>
    )
}

const headerStyle = {

    background: 'black',
    textAlign: 'center',
    color: 'yellow',
    padding: '40px',
    margin:'30px',
    borderRadius: '50px',
    fontSize:'30px'


}


const linkStyle = {

    color:'white',
    
    fontSize:'25px',
    
    


}
export default Header
